<template>
<v-app light fill-height="true">
	<v-toolbar class="primary white--text">

		<v-toolbar-title> Event-</v-toolbar-title>
		<v-spacer></v-spacer>
		 
    <v-btn flat @click.native.stop="$store.commit('speedup')" class="white--text" >
      <v-icon class="white--text" v-for="i in $store.state.speed" key="i">keyboard_arrow_right</v-icon>
    </v-btn>
	</v-toolbar>

	<main>
		<v-container>
			<v-layout row wrap>

				<v-flex xs4>
					<v-card>
            <v-toolbar class="accent white--text">
              <v-toolbar-title></v-toolbar-title>
            </v-toolbar>
						<v-list>

							<v-list-tile v-tooltip:top="{html: '-' + $store.getters.outcome.total + '  '}">
								<v-list-tile-action>
									<v-icon class="orange--text">monetization_on</v-icon>
								</v-list-tile-action>
								<v-list-tile-content>
									<v-list-tile-title>{{$store.state.sc}} Smile Coins</v-list-tile-title>
								</v-list-tile-content>
							</v-list-tile>

							<v-list-tile>
								<v-list-tile-action>
									<v-icon primary>timer</v-icon>
								</v-list-tile-action>
								<v-list-tile-content>
									<v-list-tile-title>{{$store.state.t}} </v-list-tile-title>
								</v-list-tile-content>
                <v-list-tile-action>
                  <v-progress-circular :rotate="-90" :width="2" :value="timer / 24 * 100" class="primary--text"></v-progress-circular>
                </v-list-tile-action>
							</v-list-tile>

              <v-list-tile v-tooltip:top="{html: ' : ' + $store.getters.sr.toFixed() + ' %'}">
                <v-list-tile-action>
                  <v-icon success>person</v-icon>
                </v-list-tile-action>
                <v-list-tile-content>
                  <v-list-tile-title>{{$store.state.sp}} </v-list-tile-title>
                </v-list-tile-content>
              </v-list-tile>

              <v-list-tile v-tooltip:top="{html: ' : ' + $store.getters.cm.toFixed() + ' %'}">
                <v-list-tile-action>
                  <v-icon info>group</v-icon>
                </v-list-tile-action>
                <v-list-tile-content>
                  <v-list-tile-title>{{$store.state.cl}} </v-list-tile-title>
                </v-list-tile-content>
              </v-list-tile>

              <v-list-tile v-tooltip:top="{html: ' : ' + $store.getters.sm.toFixed() + ' %'}">
                <v-list-tile-action>
                  <v-icon error>star</v-icon>
                </v-list-tile-action>
                <v-list-tile-content>
                  <v-list-tile-title>{{$store.state.sps}} </v-list-tile-title>
                </v-list-tile-content>
              </v-list-tile>

							<v-divider></v-divider>

              <v-list-tile>
								<v-list-tile-action class="caption flex xs6">
									<strong> </strong>
								</v-list-tile-action>
                <v-list-tile-content>
									<v-progress-linear secondary :value="$store.getters.sr" height="3"></v-progress-linear>
                </v-list-tile-content>
								<v-list-tile-action class="caption">
									{{$store.getters.sr}} %
								</v-list-tile-action>
              </v-list-tile>

              <v-list-tile v-tooltip:top="{html: '+' + $store.getters.income.sdr + '  '}">
								<v-list-tile-action class="caption flex xs6">
									 
								</v-list-tile-action>
                <v-list-tile-content>
									<v-progress-linear success :value="$store.state.sdr" height="3"></v-progress-linear>
                </v-list-tile-content>
								<v-list-tile-action class="caption">
									{{$store.state.sdr}} %
								</v-list-tile-action>
              </v-list-tile>

              <v-list-tile v-tooltip:top="{html: '+' + $store.getters.income.slr + '  '}">
								<v-list-tile-action class="caption flex xs6">
									 
								</v-list-tile-action>
                <v-list-tile-content>
									<v-progress-linear info :value="$store.state.slr" height="3"></v-progress-linear>
                </v-list-tile-content>
								<v-list-tile-action class="caption">
									{{$store.state.slr}} %
								</v-list-tile-action>
              </v-list-tile>

              <v-list-tile v-tooltip:top="{html: '+' + $store.getters.income.ser + '  '}">
								<v-list-tile-action class="caption flex xs6">
									 
								</v-list-tile-action>
                <v-list-tile-content>
									<v-progress-linear error :value="$store.state.ser" height="3"></v-progress-linear>
                </v-list-tile-content>
								<v-list-tile-action class="caption">
									{{$store.state.ser}} %
								</v-list-tile-action>
              </v-list-tile>

              <v-list-tile v-tooltip:top="{html: '+' + $store.getters.income.scr + '  '}">
								<v-list-tile-action class="caption flex xs6">
									 
								</v-list-tile-action>
                <v-list-tile-content>
									<v-progress-linear warning :value="$store.state.scr" height="3"></v-progress-linear>
                </v-list-tile-content>
								<v-list-tile-action class="caption">
									{{$store.state.scr}} %
								</v-list-tile-action>
              </v-list-tile>

						</v-list>

					</v-card>

				</v-flex>

				<v-flex xs4>
          <v-card>
            <v-toolbar class="accent white--text">
              <v-toolbar-title></v-toolbar-title>
            </v-toolbar>
						<v-list>
							<v-list-tile
								v-for="spec, key in config.specs"
								key="key"
								v-tooltip:top="{html: '  ' + spec.cost + ' SC  '}"
								@click.native="hire(key)"
							>
								<v-list-tile-action>
									<v-icon :class="spec.color + '--text'">{{spec.icon}}</v-icon>
								</v-list-tile-action>
								<v-list-tile-content>
									<v-list-tile-title>{{spec.title}}</v-list-tile-title>
								</v-list-tile-content>
                <v-list-tile-action class="flex xs4">
                  <v-list-tile-action-text v-if="$store.state.specs[key] > 0">
                    <v-icon>people</v-icon>
                    {{$store.state.specs[key]}}
                  </v-list-tile-action-text>
                </v-list-tile-action>
                <v-list-tile-action class="flex xs4">
                  <v-list-tile-action-text v-if="$store.state.specs[key] > 0">
                  	<v-icon>monetization_on</v-icon>
                    -{{$store.getters.outcome[key]}}
                  </v-list-tile-action-text>
                </v-list-tile-action>
							</v-list-tile>
						</v-list>
					</v-card>

          <v-card class="mt-2">
            <v-card-text>
              <v-text-field type="number" step="50" min="50" max="10000" :label="' '" :value="$store.state.p" @input="setPrice"></v-text-field>
            </v-card-text>
          </v-card>

        </v-flex>

				<v-flex xs4>
          <v-card>
            <v-toolbar class="accent white--text">
              <v-toolbar-title></v-toolbar-title>
            </v-toolbar>

						<v-card flat v-for="(message, key, index) in $store.getters.invertMessages" key="index">
							<v-card-text>
								<v-icon>{{message.icon}}</v-icon>
								{{message.text}}
							</v-card-text>
						</v-card>

					</v-card>
        </v-flex>
			</v-layout>
		</v-container>
	</main>

	<v-dialog v-model="showDialog" persistent>
		<v-card>
			<v-card-title>
				<div class="headline">{{$store.getters.isGameOver.win ? " !" : " !"}}</div>
			</v-card-title>
			<v-card-text>
				{{$store.getters.isGameOver.message}}
			</v-card-text>
			<v-card-actions>
				<v-btn flat @click.native="reload"></v-btn>
			</v-card-actions>
		</v-card>
	</v-dialog>

</v-app>
</template>

<script>
import config from './store/config'

export default {
	data() {
		return {
      config,
			timer: 0,
			showDialog: false
		}
	},
  methods: {
    hire(spec) {
      this.$store.dispatch('hire', spec);
    },
		setPrice(val) {
			this.$store.commit('setPrice', val)
		},
		reload() {
			window.location.reload()
		}
  },
  created () {
    let $this = this;
		let count = 0;
    let loop = setInterval(function () {
			if ($this.$store.getters.isGameOver != false) {
				clearInterval(loop);
				$this.showDialog = true;
			}
			count = (count + 1) % (4 - $this.$store.state.speed);
			if (count == 0) {
				$this.timer += 1;
			}
			if ($this.timer >= 24) {
				$this.timer = 0;
				$this.$store.dispatch('dailyUpdate')
			}
    }, 200)
  }
}
</script>

<style lang="stylus">
  @import "./stylus/main.styl"

	.progress-circular__overlay {
		transition: none;
	}
</style>



