import Vue from 'vue'
import Vuex from 'vuex'
import config from './config'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    sc: config.startMoney, // Smile Coins
    t: 90, // Time to deadline
    p: 500, // Ticket Price
    speed: 1, // Game speed
    sp: 0, // Speakers count
    cl: 0, // Clients Count
    sps: 0, // Sponsors Count
    sdr: 0, // Site design ready
    slr: 0, // Site layout ready
    ser: 0, // Site engine ready
    scr: 0, // Site content ready
    specs: { // Specialists
      "sales" : 0,
      "designer" : 0,
      "frontender" : 0,
      "developer" : 0,
      "content" : 0
    },
    messages: []
  },
  mutations: {
    transaction (state, amount) {
      state.sc += amount
    },
    speedup (state) {
      state.speed = state.speed % 3 + 1
    },
    addSpec (state, specName) {
      state.specs[specName] += 1;
    },
    addIncome(state, obj) {
      for (var key in obj) {
        state[key] += obj[key]
        state[key] = Math.min(100, state[key])
      }
    },
    addSpeaker(state, amount) {
      state.sp += amount;
    },
    addClient(state, amount) {
      state.cl += amount;
    },
    addSponsor(state) {
      state.sps++;
    },
    setPrice(state, value) {
      state.p = value
    },
    addMessage(state, msg) {
      state.messages.push(msg)
    }
  },
  actions: {
    dailyUpdate({state, commit, getters}) {
      state.t--;

      if (getters.outcome.total != 0) {
        commit('transaction', -1 * getters.outcome.total)
      }

      let obj = {}
      for (var key in getters.income) {
        obj[key] = getters.income[key]
      }

      commit('addIncome', obj)

      if (state.specs.sales > 0) {
        if (Math.random() < (getters.sr / 100)) {
          let num = Math.ceil(Math.random() * state.specs.sales)
          commit('addSpeaker', num)
          commit('addMessage', {
            icon: 'person',
            text: '      (' + num + ')'
          })
        }

        if (Math.random() < getters.sm / 100) {
          let amount = Math.ceil(Math.random() * state.specs.sales) * 1000
          commit('addSponsor')
          commit('transaction', amount)
          commit('addMessage', {
            icon: 'star',
            text: '       ' + amount + ' SC'
          })
        }

        if (Math.random() < (getters.sm / 100)) {
          let num = Math.ceil(Math.random() * state.specs.sales * 3);
          commit('addClient', num)
          commit('transaction', num * state.p)
          commit('addMessage', {
            icon: 'group',
            text: '     (' + num + ')'
          })
        }
      }
    },
    hire ({state, commit}, specName) {
      commit('addSpec', specName)
      commit('addMessage', {
        icon: 'person_add',
        text: '   (' + config.specs[specName].title + ')  ' + config.specs[specName].cost + ' SC  '
      })
    }
  },
  getters: {
    outcome (state) {
      let ret = {
        total: 0
      }
      for (var spec in state.specs) {
        let amount = state.specs[spec] * config.specs[spec].cost
        ret[spec] = amount
        ret.total += amount
      }
      return ret;
    },
    income (state) {
      let ret = {
        sdr: 0,
        slr: 0,
        ser: 0,
        scr: 0
      }
      for (var spec in state.specs) {
        if (state.specs[spec] > 0 && config.specs[spec].zone) {
          let zoneNeed = config.specs[spec].zoneNeed

          if (!zoneNeed || state[zoneNeed] >= config.specs[spec].zoneNeedMin) {

            let zone = config.specs[spec].zone
            let amount = config.specs[spec].amount
            ret[zone] = amount * state.specs[spec]

          }
        }
      }
      return ret;
    },
    sr (state) {
      return (state.sdr + state.slr + state.ser + state.scr) / 4;
    },
    sm (state, getters) {
      return Math.min(75, state.sp / 10 * getters.sr)
    },
    cm (state, getters) {
      return Math.min(95, (1 / state.p) * getters.sr * state.sp * 10)
    },
    isGameOver (state, getters) {
      if (state.sc <= 0) {
        return {
          win: false,
          message: "   "
        }
      }

      if (state.t == 0) {
        if (getters.sr < 100) {
          return {
            win: false,
            message: "      "
          }
        }

        let profit = state.sc - config.startMoney

        if (profit < 0) {
          return {
            win: false,
            message: "  ,  "
          }
        }

        return {
          win: true,
          message: " " + profit + " SC"
        }
      }

      return false;
    },
    invertMessages (store) {
      let arr = [...store.messages]
      return arr.reverse()
    }
  }
})



